import React, { useState } from 'react';
import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Input,
  VStack,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Text,
  Spinner,
} from '@chakra-ui/react';

const MemberSearch = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const toast = useToast();

  const handleSearch = async () => {
    if (!searchTerm.trim()) {
      toast({
        title: '',
        description: '',
        status: 'warning',
        duration: 3000,
        isClosable: true,
      });
      return;
    }

    setIsLoading(true);
    try {
      const response = await fetch(`http://localhost:5000/api/members/search?term=${encodeURIComponent(searchTerm)}`);
      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || '');
      }

      setSearchResults(data);
    } catch (error) {
      toast({
        title: '',
        description: error.message,
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box width="100%" maxW="800px">
      <VStack spacing={6}>
        <FormControl>
          <FormLabel></FormLabel>
          <Box display="flex" gap={2}>
            <Input
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder=""
            />
            <Button
              colorScheme="blue"
              onClick={handleSearch}
              isLoading={isLoading}
            >
              
            </Button>
          </Box>
        </FormControl>

        {isLoading ? (
          <Box textAlign="center" py={4}>
            <Spinner />
            <Text mt={2}>...</Text>
          </Box>
        ) : searchResults.length > 0 ? (
          <Box overflowX="auto" width="100%">
            <Table variant="simple">
              <Thead>
                <Tr>
                  <Th></Th>
                  <Th></Th>
                  <Th></Th>
                  <Th></Th>
                  <Th></Th>
                  <Th></Th>
                  <Th></Th>
                </Tr>
              </Thead>
              <Tbody>
                {searchResults.map((member) => (
                  <Tr key={member.memberId}>
                    <Td>{member.memberId}</Td>
                    <Td>{member.chineseName}</Td>
                    <Td>{member.englishName || '-'}</Td>
                    <Td>{member.gender}</Td>
                    <Td>{member.birthYear}</Td>
                    <Td>{member.idStatus}</Td>
                    <Td>{member.economicStatus}</Td>
                  </Tr>
                ))}
              </Tbody>
            </Table>
          </Box>
        ) : searchTerm ? (
          <Text color="gray.500" textAlign="center">
            
          </Text>
        ) : null}
      </VStack>
    </Box>
  );
};

export default MemberSearch; 