import React, { useState } from 'react';
import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Input,
  VStack,
  useToast,
  Select,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  useDisclosure,
} from '@chakra-ui/react';
import RegistrationSuccess from './RegistrationSuccess';

const RegisterForm = () => {
  const [formData, setFormData] = useState({
    chineseName: '',
    englishName: '',
    birthYear: '',
    gender: '',
    idStatus: '',
    economicStatus: '',
    phone: '',
    address: '',
    residenceType: '',
    maritalStatus: '',
  });
  const [isLoading, setIsLoading] = useState(false);
  const [registeredMemberId, setRegisteredMemberId] = useState('');
  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleNumberChange = (name, value) => {
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const response = await fetch('http://localhost:5000/api/members/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (!response.ok) {
        if (response.status === 409) {
          toast({
            title: '',
            description: '',
            status: 'error',
            duration: 3000,
            isClosable: true,
          });
        } else {
          throw new Error(data.message || '');
        }
        return;
      }

      setRegisteredMemberId(data.memberId);
      onOpen();

      // 
      setFormData({
        chineseName: '',
        englishName: '',
        birthYear: '',
        gender: '',
        idStatus: '',
        economicStatus: '',
        phone: '',
        address: '',
        residenceType: '',
        maritalStatus: '',
      });
    } catch (error) {
      toast({
        title: '',
        description: error.message,
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleSuccessClose = () => {
    onClose();
    setRegisteredMemberId('');
  };

  return (
    <>
      <Box as="form" onSubmit={handleSubmit} width="100%" maxW="600px">
        <VStack spacing={4}>
          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Input
              name="chineseName"
              value={formData.chineseName}
              onChange={handleChange}
              placeholder=""
            />
          </FormControl>

          <FormControl>
            <FormLabel></FormLabel>
            <Input
              name="englishName"
              value={formData.englishName}
              onChange={handleChange}
              placeholder=""
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <NumberInput
              value={formData.birthYear}
              onChange={(value) => handleNumberChange('birthYear', value)}
              min={1900}
              max={new Date().getFullYear()}
            >
              <NumberInputField placeholder="" />
              <NumberInputStepper>
                <NumberIncrementStepper />
                <NumberDecrementStepper />
              </NumberInputStepper>
            </NumberInput>
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Select
              name="gender"
              value={formData.gender}
              onChange={handleChange}
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Select
              name="idStatus"
              value={formData.idStatus}
              onChange={handleChange}
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Select
              name="economicStatus"
              value={formData.economicStatus}
              onChange={handleChange}
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </Select>
          </FormControl>

          <FormControl>
            <FormLabel></FormLabel>
            <Input
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder=""
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Input
              name="address"
              value={formData.address}
              onChange={handleChange}
              placeholder=""
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Select
              name="residenceType"
              value={formData.residenceType}
              onChange={handleChange}
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
            </Select>
          </FormControl>

          <FormControl isRequired>
            <FormLabel></FormLabel>
            <Select
              name="maritalStatus"
              value={formData.maritalStatus}
              onChange={handleChange}
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
              <option value="/">/</option>
            </Select>
          </FormControl>

          <Button
            type="submit"
            colorScheme="blue"
            width="100%"
            isLoading={isLoading}
          >
            
          </Button>
        </VStack>
      </Box>

      <Modal isOpen={isOpen} onClose={handleSuccessClose} isCentered>
        <ModalOverlay />
        <ModalContent>
          <ModalBody p={6}>
            <RegistrationSuccess
              memberId={registeredMemberId}
              onClose={handleSuccessClose}
            />
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
};

export default RegisterForm; 